@import './reset';
@import './animation';


* {
    font-family: 'Montserrat', 'Arial';
}

body {
    background-color: white;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;      
} 


      
code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
       monospace;
}

html {
    font-size: 20px;
}


.mobile-nav {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 1000;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(0,0,0,0.9);
    transform: translateX(100%);
    transition: transform 0.2s linear;

    &-content {
        position: relative;
        display: flex;
        justify-content: center;
        align-items: center;
        max-width: 440px;
        width: 100%;
        height: 100%;
        span {
            position: absolute;
            font-size: 3rem;
            color: #ffffff;
            text-shadow: 0 0 2px #000000;
        }

        ul li {
            text-align: center;
            margin-top: 15px;
            a {
                font-size: 2.5rem;
                color: #ffffff;
                text-shadow: 0 0 2px #000000;
            }

            &:last-of-type {
                text-decoration: underline;
            }
        }
    }
}

.wrapper {
    max-width: 1920px;
    margin: 0 auto;
}

.header {
    display: flex;
    flex-flow: column;
    justify-content: space-between;
    height: 980px;
    max-width: 1730px;
    margin: 0 auto;
    padding: 0 15px;
    nav {
        .burger {
            display: none;
        }

        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 100%;
        padding-left: 20px;
        margin-top: 35px;

        .logo {
            width: 43px;
            height: 41px;
        }

        .nav-links {
            display: flex;
            justify-content: space-between;
            align-items: center;
            width: 100%;
            max-width: 885px;
            ul {
                display: flex;
                justify-content: space-between;
                max-width: 520px;
                width: 100%;
                li {
                    transition: 0.2s opacity;
                    a {
                        font-size: 1.2rem;
                        color: #000000;
                        line-height: 1.9rem;
                    }
                    
                    &:hover {
                        opacity: 0.5;
                    }
                }
            }

            .contact-button {
                display: flex;
                justify-content: center;
                align-items: center;
                width: 296px;
                height: 64px;
                font-size: 1.2rem;
                line-height: 1.9rem;
                font-weight: 700;
                color: #ffffff;
                background-color: #7c91d2;
                border-radius: 5px;
                opacity: 0.9;
                transition: 0.2s opacity;

                &:hover {
                    opacity: 1;
                }
            }
        }
    }

    &-content {
        display: flex;
        justify-content: space-between;
        align-items: center;

        .header-text {
            h3 {
                font-size: 5.75rem;
                font-weight: 900;
                line-height: 6.5rem;
            }

            h3:first-of-type {
                color: #7c91d2;
            }

            h3:last-of-type {
                color: #000000;
            }

            .smm-strategy {
                display: block;
                margin: 50px auto 0;
                max-width: 610px;
                width: 100%;

                p {
                    font-size: 1.2rem;
                    color: #000000;
                    line-height: 1.9rem;
                    opacity: 0.75;
                }
            }
        }

        .header-image {
            margin-right: 100px;

            img {
                width: 100%;
                max-width: 523px;
                height: auto;
            }
        }
    }
    .arrow-down {
        position: relative;
        margin-bottom: 50px;
        height: 50px;

        a {
            position: absolute;
            top: 0;
            left: 50%;
            display: flex;
            justify-content: center;
            align-items: center;
            width: 50px;
            height: 50px;
            border-radius: 50%;
            border: 1px solid #000000;
            transform: translateX(-50%);

            animation: bounce;
            animation-duration: 2s;
            animation-iteration-count: infinite;
            animation-timing-function: ease-in-out;
        }
    }
}

.main {
    .we-in-numbers {
        display: flex;
        justify-content: center;
        align-items: center;
        position: relative;
        background-position: center center;
        background-size: cover;

        h2 {
            position: absolute;
            top: 100px;
            left: 110px;
            font-size: 2.4rem;
            line-height: 3.7rem;
            font-weight: 700;
            color: #ffffff;
        }

        ul {
            display: flex;
            flex-wrap: wrap;
            justify-content: space-between;
            max-width: 1500px;
            width: 100%;
            margin: 350px 0;
            padding-right: 65px;

            li {
                color: #ffffff;
                text-align: center;

                p {
                    font-size: 8rem;
                    font-weight: 900;
                    line-height: 1;
                }

                span {
                    // margin-top: 30px;
                    font-size: 2.4rem;
                    font-weight: 600;
                    line-height: 2rem;
                }

                .numbers-percent {
                    position: relative;
                    &::after {
                        position: absolute;
                        bottom: 10px;
                        right: -65px;
                        font-size: 3.2rem;
                        content: '%';
                    }
                }
            }            
        }
    }

    .how-we-work {
        padding: 100px 240px 0 120px;        
        background-position: 180px 210px;
        background-repeat: no-repeat;
        color: #000000;

        .explanation-text {
            margin: 120px 0 0 140px;
            p {
                font-size: 1.8rem;
                font-weight: 700;
                line-height: 2.2rem;
            }
        }

        .work-stages {
            display: flex;
            justify-content: space-between;
            align-items: center;
            margin-top: 100px;
            width: 100%;
            li {
                display: flex;
                align-items: center;

                p {
                    display: inline-block;
                    max-width: 200px;
                    margin-left: 40px;
                    font-size: 1.2rem;
                    line-height: 1.8rem;
                    font-weight: 400;
                }
            }
        }
    }

    .our-services {
        margin-top: 200px;
        padding: 0 50px 0 120px;
        
        .slider {
            margin: 100px 0 0 -10px;
            cursor:  grab;
            
            .services {
                position: relative;
                padding: 0 25px;
                // margin-left: 50px;
                max-width: 500px;
                height: 450px;
                color: #000000;
                border-radius: 15px;
                box-shadow: 0 12px 30px rgba(9, 2, 27, 0.15);
                transition: 0.2s color;
                overflow: hidden;

                // добавляю фоновый блок
                &::after {
                    position: absolute;
                    top: 0;
                    left: 0;
                    right: 0;
                    bottom: 0;
                    z-index: -1;
                    background-image: linear-gradient(-45deg, #495fa1 0%, #7c91d2 100%);
                    opacity: 0;      
                    transition: 0.2s opacity;
                    // border-radius: 15px;
                    content: '';
                }                

                &:first-of-type {
                    margin-left: 10px;
                }

                &:hover {
                    color: #ffffff;                
                    p {
                        opacity: 0.75;
                    }
                }

                &:hover::after {
                    opacity: 1;
                }

                &:focus {
                    outline: none;
                }
                
                h3 {
                    margin-top: 48px;
                    font-size: 1.6rem;
                    font-weight: 700;
                }

                .service-info {
                    margin-top: 65px;
                    p {
                        font-size: 1.2rem;
                        line-height: 1.9rem;
                        opacity: 0.5;
                    }
                }
            }
        }
    }

    .cases {
        max-width: 1680px;
        margin: 0 auto;
        ul {
            display: flex;
            flex-wrap: wrap;
            justify-content: space-between;
            width: 100%;
            margin-top: 50px;
            li {
                position: relative;
                margin-top: 50px;
                max-width: 500px;
                width: 100%;
                height: 500px;
                background-image: linear-gradient(-45deg, #495fa1 0%, #7c91d2 100%);
                box-shadow: 0 12px 30px rgba(9, 2, 27, 0.15);
                border-radius: 15px;
                overflow: hidden;
                transition: opacity 0.2s;
                cursor: pointer;

                a {
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    width: 100%;
                    height: 100%;                    
                    font-size: 1.6rem;
                    font-weight: 700;
                    color: #ffffff;
                    opacity: 0;
                    background-color: rgba(0,0,0, 0.5);
                    transition: opacity 0.2s;
                }
                
                &:hover a {
                    opacity: 1;
                }                
            }            
            
            .link-to-journal {
                a {
                    background-color: transparent;
                    opacity: 1;
                }
            }

            .link-to-journal:hover {
                background-image: linear-gradient(-45deg, darken(#495fa1, 5%) 0%, darken(#7c91d2, 5%) 100%);
                
                a {
                    color: darken(#ffffff, 5%);
                }
            }   

            .load-more-articles {
                display: flex;
                flex-direction: column;
                align-items: center;
                justify-content: center;
                
                background-image: none;
                box-shadow: none;
                p {
                    margin-top: 30px;
                    font-size: 1.6rem;
                    font-weight: 700;
                    color: #000000;
                    opacity: 0.5;
                }

                svg {
                    transition: transform 0.2s;
                }

                &:hover p {
                    opacity: 0.8;
                }
                &:hover svg {
                    transform: translateX(10px);
                }
            }
        }
    }
    
    .additional-resources {
        max-width: 1680px;
        margin: 100px auto 0;
        width: 100%;
        ul {
            display: flex;
            width: 100%;
            justify-content: space-between;

            li {
                width: 49%;
            }

            a {
                position: relative;
                display: flex;
                justify-content: space-between;
                align-items: flex-end;
                padding: 30px 40px 30px 140px;
                width: 100%;
                height: 100%;
                border-radius: 15px;
                box-shadow: 0 12px 30px rgba(9, 2, 27, 0.15);
                overflow: hidden;
                cursor: pointer;

                &::before {
                    position: absolute;
                    top: 0;
                    left: 0;
                    right: 0;
                    bottom: 0;
                    z-index: -1;
                    opacity: 0;
                    background-image: linear-gradient(-45deg, #495fa1 0%, #7c91d2 100%);
                    transition: 0.2s opacity;
                    content: '';
                }

                .additional-info-content {               
                    color: #010101;
                    font-size: 1.2rem;
                    h3 {
                        font-weight: 700;
                    }

                    p {
                        font-weight: 400;
                        line-height: 1.9rem;
                        opacity: 0.75;

                        &:first-of-type {
                            margin-top: 10px;
                        }
                    }
                }

                span {
                    position: relative;
                    display: block;
                    margin-bottom: 25px;
                    height: 3px;
                    width: 64px;
                    background-color: #060606;
                    transform: rotateY(180deg);
                    transition: 0.2s width;
                    

                    &::after {
                        position: absolute;
                        bottom: -9px;
                        left: -6px;
                        height: 3px;
                        width: 30px;
                        transform: rotateZ(40deg);
                        background-color: inherit;
                        content: '';
                    }

                    &::before {                        
                        position: absolute;
                        top: -9px;
                        left: -6px;
                        height: 3px;
                        width: 30px;
                        transform: rotateZ(-40deg);
                        background-color: inherit;
                        content: '';
                    }

                }
                // mouse over the box
                &:hover::before{
                    opacity: 1;
                }

                &:hover {                    
                    .additional-info-content {
                        color: #ffffff;
                    }

                    span {
                        width: 100px;
                        background-color: #ffffff;
                    }
                }
            }
        }
    }

    .free-consult {
        max-width: 1730px;
        margin: 200px auto 0;
        padding-top: 70px;
        background-position: 90px 0;
        background-repeat: no-repeat;

        &-text {
            margin-left: 160px;
            p {
                font-size: 1.8rem;
                font-weight: 700;
                line-height: 48px;
                color: #000000;
            }
        }
    }   
    
    .contact-form {
        margin: 290px auto 0;
        max-width: 1200px;

        h3 {
            font-size: 2.4rem;
            font-weight: 700;
            color: #000000;
        }

        form {
            display: flex;
            flex-wrap: wrap;
            justify-content: space-between;
            margin-top: 50px;

            .form-input {
                max-width: 570px;
                width: 100%;
                margin-top: 40px;
                padding: 20px;
                font-size: 1.2rem;
                font-weight: 400;
                border: none;
                border-bottom: 3px solid #000000;
                transition: border-color 1s;

                &:focus {
                    outline: none;
                }
            }
            .form-input::placeholder {
                font-size: 1.2rem;
                color: #000000;
            }
        }        

        .send-form {
            display: block;
            margin: 90px auto 0;
            padding: 20px 60px;
            font-size: 1.2rem;
            font-weight: 700;
            color: #fefefe;
            background-color: #7c91d2;
            border-radius: 5px;
            border: none;
            opacity: 0.9;
            transition: opacity 0.2s;
            cursor: pointer; 

            &:hover {
                opacity: 1;
            }
        }
    }
}
    
.footer {
    margin-top: 120px;
    padding: 75px 0 90px;
    background-color: #000000;

    .footer-content {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin: 0 auto;
        max-width: 1550px;
        width: 100%;

        .footer-author {
            display: flex;
            justify-content: center;
            align-items: flex-end;

            &-text {
                margin: -15px 0 0 25px;
            }

            svg {
                height: 41px;
                width: 43px;
                transform: translateY(-5px);
            }
        }

        .footer-socials {
            .social-links {
                margin-top: 50px;
                display: flex;
                justify-content: space-between;   

                li {
                    margin-left: 40px;
                    svg {
                        height: 40px;
                        width: 41px;
                        transition: opacity 0.2s;

                        path {
                            fill: #ffffff;
                        }
                    }

                    &:first-of-type {
                        margin: 0;
                    }

                    &:hover {
                        svg {
                            opacity: 0.6;
                        }
                    }
                }
            }

            p {
                margin-top: 55px;
                font-size: 1.2rem;
                line-height: 38px;
                color: #ffffff;
                opacity: 0.25;
            }
        }
    }

    // style for P elements
    .footer-p-common-styles {
        margin-top: 12px;
        font-size: 1.2rem;
        font-weight: 400;
        color: #ffffff;
    }
    
    // style for H2 elements 
    .footer-block-name {
        margin-bottom: 40px;
        font-size: 1.2rem;
        font-weight: 700;
        color: #9aa9dc;        
    }
}

.arrow-to-up {
    position: fixed;
    bottom: 50px;
    right: 50px;
    display: none;
    z-index: 999;
    font-size: 70px;
    opacity: 0.5;
    color: #000000;
    transition: opacity 0.2s;
    text-shadow: 0 0 1px #ffffff;

    &:hover {
        opacity: 1;
    }
}

.visible {
    display: inline;
}

.block-name {
    font-size: 2.4rem;
    font-weight: 700;
}

.slick-list {
    padding: 10px 0;
}

.red-border {
    border-color: red !important;
}

